/**
 * Function to convert jquery Ajax style configuration to work with Vanilla JS fetch()
 * To see parameters for settings accepts, see: http://api.jquery.com/jquery.ajax/
 * @param settings
 * @return Promise<Response>
 */
function ajax(settings) {
    let async = true;
    let method = 'method' in settings ? settings.method : 'POST';

    if (!'url' in settings || typeof settings.url === 'undefined' || settings.url === null) {
        throw new Error('No url is set');
    }
    if (method !== 'GET' && (!'data' in settings || typeof settings.data === 'undefined' || settings.data === null)) {
        throw new Error('No data is set');
    }
    if (!'success' in settings || typeof settings.success === 'undefined' || settings.success === null) {
        throw new Error('No success handler is set');
    }
    if (!'error' in settings || typeof settings.error === 'undefined' || settings.error === null) {
        throw new Error('No error handler is set');
    }
    if ('async' in settings || typeof settings.async !== 'undefined' || settings.async === null) {
        async = settings.async;
    }

    let formData = settings.data instanceof FormData ? settings.data : new FormData;
    if (settings.data !== null && !(settings.data instanceof FormData)) {
        Object.keys(settings.data).forEach(data => {
            let value = settings.data[data];
            formData.append(data, value);
        });
    }

    let ajaxRequest = fetch(settings.url, {
            method: method,
            body: formData
        })
        .then(response => {
            if (response.ok) {
                return Promise.resolve(response);
            } else {
                return Promise.reject(new Error('Failed to load'));
            }
        })
        // parse response to json format
        .then(response => response.json()) // parse response as JSON
        // handle return data and do stuff
        .then(data => {
            let result = settings.success(data);
            if (typeof result !== 'undefined') {
                return result;
            }
        })
        .catch(function (error) {
            let result = settings.error(error);
            if (typeof result !== 'undefined') {
                return result;
            }
        });

    let ajaxResponse;
    if (async) {
         ajaxResponse = async () => {
            return await ajaxRequest;
        }
        return ajaxResponse();
    } else {
        ajaxResponse = ajaxRequest;
        return ajaxResponse;
    }
}