/**
 * Custom form validation class
 *
 * @Author: Jory Vennix - BlueMinds BV
 * 2023
 */

class BluemindsFormValidator {
    constructor (form, rules) {
        if (typeof form === 'undefined' || form === null) { throw new Error('Please provide form in order to validate'); }
        if (typeof rules === 'undefined' || rules === null) { throw new Error('Please provide rules in orde to validate'); }

        this.form = form;
        this.rules = rules;
        this.valid = true;
        this.invalidInputs = [];
    }

    /**
     * Function to validate all form elements provided in rules object
     */
    validate() {
        // herstellen alle gevalideerde velden
        this.resetFormValidation();

        if (Object.keys(this.rules).length > 0) {
            Object.keys(this.rules).forEach(key => {
                /**
                 * store input associated with rule
                 * this could potentially be more than 1 input in case of radio buttons
                 */
                let input = this.form.querySelector('[name="' + key + '"]');
                this.validateInput(input, this.rules[key]);
            });
        }

        // after we check validation rules we check if there are any other .is-invalid elements present in form
        let additionalInvalidFields = this.form.querySelectorAll('.is-invalid');
        if (additionalInvalidFields.length) {
            this.valid = false;
        }

        return this.valid;
    }

    /**
     * Function to reset validation on form; invalid classes, etc.
     */
    resetFormValidation() {
        this.valid = true;
        this.invalidInputs = [];

        // remove invalid classes from form inputs
        let elements = this.form.querySelectorAll('input, select, label');
        elements.forEach(element => {
            element.classList.remove('is-invalid');
            element.parentNode.classList.remove('is-invalid');
        });

        this.form.querySelectorAll('.blueminds_form_validation-error_message').forEach(elem => {
            elem.remove();
        });
    }

    validateInput(input, rules)
    {
        // store rule keys to iterate through but also for future reference
        let inputRuleKeys = Object.keys(rules);
        inputRuleKeys.forEach(rule => {
            // to prevent invalidating and revalidating for multiple cases in validation we check if form not already is invalid
            if (this.invalidInputs.includes(input.getAttribute('name'))) { return true; }

            // get rule value
            let value = rules[rule];

            // check if value is function
            if (typeof value === 'function') {
                // when is function get the actual value. We also pass inputs value to function
                value = value();
            }

            // determine action according to rule
            switch (rule) {
                case 'required':
                    this.handleRequired(input, value);
                    break;
                case 'remote':
                    /** For remote we use an Ajax style configuration to perform build-in converter function ajax() */
                    this.handleRemote(input, value);
                    break;
                case 'email':
                    this.handleEmail(input, value);
                    break;
                case 'conditional':
                    this.handleConditional(input, value);
                    break;
            }
        });
    }

    handleRequired(input, value)
    {
        // when required, check if rule is set to true and input value is not empty
        switch (input.getAttribute('type')) {
            case 'radio':
                // in case of radio there could potentially be more than 1 input containing the same name
                let radios = this.form.querySelectorAll('[name="' + input.getAttribute('name') + '"]');
                // we also need check if one of the radios is checked
                let radioChecked = this.form.querySelector('[name="' + input.getAttribute('name') + '"]:checked');

                if (value && radioChecked === null) {
                    this.invalidateInput(radios, input.getAttribute('name'));
                }
                break;
            default:
                if (value && !input.value) {
                    this.invalidateInput(input, input.getAttribute('name'));
                }
                break;
        }
    }

    handleRemote(input, value)
    {
        // check if input value is not empty otherwise it makes no sense to check remote
        if (input.value) {
            let remoteResponse = ajax(value);
            remoteResponse.then(Promise => {
                if (typeof Promise === 'string' || Promise instanceof String) {
                    this.invalidateInput(input, input.getAttribute('name'), Promise);
                }
            });
        }
    }

    handleEmail(input, value)
    {
        if (input.value) {
            // when input not empty we hold input against a regex
            const re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
            if (!re.test(String(input.value).toLowerCase())) {
                this.invalidateInput(input, input.getAttribute('name'), 'This is not a valid email address input');
            }
        }
    }

    handleConditional(input, response)
    {
        if (response.success === false) {
            this.invalidateInput(input, input.getAttribute('name'), response.message);
        }
    }

    invalidateInput(object, name, message = null)
    {
        this.valid = false;
        this.invalidInputs.push(name);

        if (NodeList.prototype.isPrototypeOf(object))
        {
            object.forEach(node => {
                node.classList.add('is-invalid');
                this.invalidateLabel(node);

                if (message !== null) {
                    this.handleInvalidMessage(node, message);
                }
            });
        } else {
            object.classList.add('is-invalid');
            this.invalidateLabel(object);

            if (message !== null) {
                this.handleInvalidMessage(object, message);
            }
        }


    }

    invalidateLabel(node)
    {
        if (node.parentNode instanceof HTMLLabelElement) {
            node.parentNode.classList.add('is-invalid');
        } else {
            node.parentNode.querySelector('label').classList.add('is-invalid');
        }
    }

    handleInvalidMessage(input, message)
    {
        let invalidMessageElement = document.createElement('span');
        invalidMessageElement.setAttribute('data-hook', input.name + '-error_message');
        invalidMessageElement.classList.add('blueminds_form_validation-error_message');
        invalidMessageElement.textContent = message;

        input.parentNode.insertBefore(invalidMessageElement, input.nextSibling);
    }
}